import './style.css'
import logoUrl from './logo.png'

const app = document.querySelector('#app')

app.innerHTML = `
  <div id="splash" class="fixed inset-0 z-50 flex items-center justify-center bg-pink-50 backdrop-blur-sm transition-opacity duration-700">
    <picture>
      <img src="${logoUrl}" alt="Logo" class="block mx-auto w-auto h-auto max-w-[90vw] max-h-[75vh] object-contain" />
    </picture>
  </div>
  <header class="py-10">
    <div class="mx-auto max-w-2xl px-4 text-center">
      <img src="${logoUrl}" alt="VetDose" class="mx-auto h-16 w-auto" />
      <p class="mt-2 text-gray-600">Calculadora de dosagem veterin√°ria</p>
    </div>
  </header>

  <main class="mx-auto max-w-2xl px-4 pb-14">
    <section class="rounded-xl bg-white/90 p-6 shadow-md ring-1 ring-black/5">
      <div id="panel"></div>
    </section>
  </main>
`

// Splash: aparece por 2 segundos e depois faz fade-out
const splashEl = document.getElementById('splash')
setTimeout(() => {
  splashEl.classList.add('opacity-0')
  setTimeout(() => {
    splashEl.remove()
  }, 700) // Aguarda o fade-out completar antes de remover
}, 2000) // 2 segundos na tela

// Tabelas de dosagem por esp√©cie (mg/kg) e metadados
// Quando existe faixa, usamos o ponto m√©dio para o c√°lculo e exibimos a faixa.
const DOSAGENS = {
  amoxicilina: {
    cachorro: { min: 10, max: 20, freq: 'a cada 12h', dur: '5‚Äì7 dias', obs: 'Uso oral comum.' },
    gato:     { min: 10, max: 20, freq: 'a cada 12h', dur: '5‚Äì7 dias', obs: 'Uso oral comum.' },
  },
  doxiciclina: {
    cachorro: { min: 5, max: 10, freq: 'a cada 12h', dur: '7‚Äì10 dias', obs: 'Pode causar fotossensibilidade.' },
    gato:     { min: 5, max: 10, freq: 'a cada 12h', dur: '7‚Äì10 dias', obs: 'Administrar com √°gua.' },
  },
  enrofloxacina: {
    cachorro: { min: 5, max: 10, freq: 'a cada 24h', dur: '5‚Äì10 dias', obs: 'Evitar em animais jovens.' },
  },
  dipirona: {
    cachorro: { min: 25, max: 25, freq: 'a cada 8h', dur: 'conforme necessidade', obs: 'Uso conforme orienta√ß√£o.' },
    gato:     { min: 12, max: 12, freq: 'a cada 8h', dur: 'conforme necessidade', obs: 'Uso conforme orienta√ß√£o.' },
  },
  ivermectina: {
    cachorro: { min: 0.2, max: 0.4, freq: 'dose √∫nica', dur: '‚Äî', obs: 'Aten√ß√£o ra√ßas sens√≠veis (MDR1).'},
    gato:     { min: 0.2, max: 0.2, freq: 'dose √∫nica', dur: '‚Äî', obs: 'Aten√ß√£o a rea√ß√µes adversas.' },
  },
  cefalexina: {
    cachorro: { min: 15, max: 30, freq: 'a cada 12h', dur: '7‚Äì14 dias', obs: 'Uso oral.' },
    gato:     { min: 15, max: 30, freq: 'a cada 12h', dur: '7‚Äì14 dias', obs: 'Uso oral.' },
  },
  meloxicam: {
    cachorro: { min: 0.2, max: 0.2, freq: '0,2 mg/kg inicial, 0,1 mg/kg manuten√ß√£o 1x/dia', dur: 'conforme orienta√ß√£o', obs: 'Anti-inflamat√≥rio n√£o esteroidal.' },
  },
  prednisona: {
    cachorro: { min: 0.5, max: 2.0, freq: 'a cada 24h', dur: 'conforme indica√ß√£o', obs: 'Ajuste conforme quadro cl√≠nico.' },
  },
  furosemida: {
    cachorro: { min: 2, max: 6, freq: 'a cada 8‚Äì12h', dur: 'conforme resposta', obs: 'Diur√©tico ‚Äî monitorar hidrata√ß√£o.' },
  },
}

const limitesPesoKg = { min: 0.1, max: 500 }

const ESPECIES = [
  { value: 'cachorro', label: 'üê∂ Cachorro' },
  { value: 'gato',     label: 'üê± Gato' },
  { value: 'coelho',   label: 'üê∞ Coelho' },
  { value: 'cavalo',   label: 'üê¥ Cavalo' },
  { value: 'ave',      label: 'üïäÔ∏è Ave' },
  { value: 'boi',      label: 'üêÆ Boi' },
  { value: 'porco',    label: 'üê∑ Porco' },
]

// Utilit√°rios globais
function round2(n) {
  return Math.round((n + Number.EPSILON) * 100) / 100
}

function formatNumber(n) {
  return n.toLocaleString('pt-BR', { maximumFractionDigits: 2, minimumFractionDigits: 0 })
}

function getRegra(medicamento, especie) {
  const mapa = DOSAGENS[medicamento]
  if (!mapa) return null
  return mapa[especie] || mapa['cachorro'] || mapa['gato'] || null
}

function renderForm(initial = {}) {
  const panel = document.getElementById('panel')
  const especie = initial.especie || 'cachorro'
  const pesoStr = initial.pesoStr || ''
  const med = initial.med || 'amoxicilina'
  const concStr = initial.concStr || ''
  const sensivel = Boolean(initial.sensivel)

  panel.innerHTML = `
      <h2 class="text-lg font-semibold text-gray-900">Calcular dosagem</h2>
      <p class="mt-1 text-sm text-gray-600">Selecione a esp√©cie, informe o peso, o medicamento e, opcionalmente, a concentra√ß√£o.</p>

      <form id="dose-form" class="mt-6 grid grid-cols-1 gap-4 sm:grid-cols-2">
        <div class="sm:col-span-2">
          <label for="animal" class="block text-sm font-medium text-gray-700">Esp√©cie</label>
          <select id="animal" class="mt-1 w-full rounded-md border-gray-300 bg-white px-3 py-2 text-gray-900 shadow-sm focus:border-pink-500 focus:outline-none focus:ring-2 focus:ring-pink-500/20">
            ${ESPECIES.map(s => `<option value="${s.value}" ${s.value===especie?'selected':''}>${s.label}</option>`).join('')}
          </select>
        </div>

        <div>
          <label for="peso" class="block text-sm font-medium text-gray-700">Peso</label>
          <div class="relative mt-1">
            <span class="pointer-events-none absolute inset-y-0 left-3 flex items-center text-gray-500">kg</span>
            <input id="peso" inputmode="numeric" autocomplete="off" placeholder="0,00" value="${pesoStr}"
              class="w-full rounded-md border-gray-300 pl-10 pr-3 py-2 text-gray-900 shadow-sm focus:border-pink-500 focus:outline-none focus:ring-2 focus:ring-pink-500/20" />
          </div>
        </div>

        <div>
          <label for="medicamento" class="block text-sm font-medium text-gray-700">Medicamento</label>
          <select id="medicamento" class="mt-1 w-full rounded-md border-gray-300 bg-white px-3 py-2 text-gray-900 shadow-sm focus:border-pink-500 focus:outline-none focus:ring-2 focus:ring-pink-500/20">
            <option value="amoxicilina" ${med==='amoxicilina'?'selected':''}>Amoxicilina</option>
            <option value="doxiciclina" ${med==='doxiciclina'?'selected':''}>Doxiciclina</option>
            <option value="enrofloxacina" ${med==='enrofloxacina'?'selected':''}>Enrofloxacina</option>
            <option value="dipirona" ${med==='dipirona'?'selected':''}>Dipirona</option>
            <option value="ivermectina" ${med==='ivermectina'?'selected':''}>Ivermectina</option>
            <option value="cefalexina" ${med==='cefalexina'?'selected':''}>Cefalexina</option>
            <option value="meloxicam" ${med==='meloxicam'?'selected':''}>Meloxicam</option>
            <option value="prednisona" ${med==='prednisona'?'selected':''}>Prednisona</option>
            <option value="furosemida" ${med==='furosemida'?'selected':''}>Furosemida</option>
          </select>
        </div>

        <div>
          <label for="concentracao" class="block text-sm font-medium text-gray-700">Concentra√ß√£o (mg/mL) ‚Äî opcional</label>
          <input id="concentracao" inputmode="decimal" placeholder="Ex.: 250" value="${concStr}" class="mt-1 w-full rounded-md border-gray-300 px-3 py-2 text-gray-900 shadow-sm focus:border-pink-500 focus:outline-none focus:ring-2 focus:ring-pink-500/20" />
        </div>

        <div class="flex items-center gap-2 sm:col-span-2">
          <input id="racaSensivel" type="checkbox" ${sensivel?'checked':''} class="h-4 w-4 rounded border-gray-300 text-pink-600 focus:ring-pink-600" />
          <label for="racaSensivel" class="text-sm text-gray-700">Ra√ßa sens√≠vel (Collie/Sheltie) ‚Äî alerta para Ivermectina</label>
        </div>

        <div class="sm:col-span-2 mt-2">
          <button type="submit" class="inline-flex w-full items-center justify-center rounded-md bg-pink-500 px-4 py-2 text-white font-medium shadow-sm transition hover:bg-pink-600 focus:outline-none focus:ring-2 focus:ring-pink-500/30">Calcular dose</button>
        </div>
      </form>

      <div id="alert" class="mt-4 hidden rounded-md border border-red-200 bg-red-50 px-4 py-3 text-sm text-red-700"></div>
  `

  const form = document.getElementById('dose-form')
  const especieSelect = document.getElementById('animal')
  const pesoInput = document.getElementById('peso')
  const medicamentoSelect = document.getElementById('medicamento')
  const concentracaoInput = document.getElementById('concentracao')
  const racaSensivel = document.getElementById('racaSensivel')
  const alertBox = document.getElementById('alert')

  function maskDecimal(el) {
    el.addEventListener('input', () => {
      let s = el.value
      s = s.replace(/[^\d,\.]/g, '')
      s = s.replace(/\./g, '')
      if (s.indexOf(',') !== -1) {
        const [int, dec] = s.split(',')
        const intFmt = int.replace(/\B(?=(\d{3})+(?!\d))/g, '.')
        el.value = dec !== undefined ? `${intFmt},${dec.replace(/[^\d]/g,'').slice(0,2)}` : intFmt
      } else {
        const intFmt = s.replace(/\B(?=(\d{3})+(?!\d))/g, '.')
        el.value = intFmt
      }
    })
  }

  // M√°scara de 2 casas decimais autom√°tica (ex.: 1250 -> 12,50)
  function maskTwoDecimals(el) {
    const format = (digits) => {
      if (!digits) return ''
      // remove zeros √† esquerda
      digits = digits.replace(/^0+(\d)/, '$1')
      if (digits.length === 1) digits = '0' + digits
      const intPart = digits.slice(0, -2) || '0'
      const decPart = digits.slice(-2)
      const intFmt = intPart.replace(/\B(?=(\d{3})+(?!\d))/g, '.')
      return `${intFmt},${decPart}`
    }
    const handler = () => {
      let s = el.value.replace(/\D/g, '')
      el.value = format(s)
    }
    el.addEventListener('input', handler)
    el.addEventListener('focus', handler)
  }

  maskTwoDecimals(pesoInput)
  maskDecimal(concentracaoInput)

  function parsePtBrNumber(str) {
    if (!str) return NaN
    return Number(str.replace(/\./g,'').replace(',', '.'))
  }

  function showAlert(message) {
    alertBox.textContent = message
    alertBox.classList.remove('hidden')
  }

  form.addEventListener('submit', (e) => {
    e.preventDefault()
    const especie = especieSelect.value
    const peso = parsePtBrNumber(pesoInput.value)
    const med = medicamentoSelect.value
    const conc = parsePtBrNumber(concentracaoInput.value)
    const sens = racaSensivel.checked
    calcular({ especie, peso, med, conc, sens, raw: {pesoStr:pesoInput.value, concStr:concentracaoInput.value} })
  })
}

function renderResultado(view) {
  const panel = document.getElementById('panel')
  panel.innerHTML = `
    <button id=\"voltar\" class=\"-ml-2 inline-flex items-center gap-2 rounded-md px-2 py-1 text-sm text-gray-700 hover:text-gray-900\"><span>‚Üê</span><span>Voltar</span></button>
    <div class=\"mt-3 rounded-xl bg-white p-6 shadow-md ring-1 ring-black/5\">
      <div class=\"text-base font-semibold\">Plano de cuidado</div>
      <div class=\"mt-1 text-sm text-gray-700\">Esp√©cie: <span class=\"font-medium\">${view.especie}</span> ‚Ä¢ Medicamento: <span class=\"font-medium capitalize\">${view.med}</span></div>

      <div class=\"mt-4 flex items-baseline gap-3\">
        <span class=\"text-sm text-gray-600\">Dose</span>
        <span class=\"text-2xl font-bold text-green-600\">${view.doseMg} mg</span>
        ${view.volumeMl ? `<span class=\\"text-sm text-gray-600\\">(‚âà ${view.volumeMl} mL)</span>` : ''}
      </div>

      <div class=\"mt-4 grid grid-cols-1 gap-2 sm:grid-cols-2\">
        <div class=\"rounded-lg bg-gray-50 px-3 py-2\"><div class=\"text-xs text-gray-500\">Frequ√™ncia</div><div class=\"font-medium\">${view.freq}</div></div>
        <div class=\"rounded-lg bg-gray-50 px-3 py-2\"><div class=\"text-xs text-gray-500\">Dura√ß√£o</div><div class=\"font-medium\">${view.dur}</div></div>
      </div>

      <div class=\"mt-4 border-t pt-4 text-sm text-gray-700\">Refer√™ncia: ${view.faixaStr}</div>
      <div class=\"mt-2 text-sm text-gray-700\">Observa√ß√£o: ${view.obs}</div>
      ${view.alerta ? `<div class=\\"mt-3 rounded border border-amber-200 bg-amber-50 px-3 py-2 text-amber-800\\">${view.alerta}</div>` : ''}
    </div>
  `
  document.getElementById('voltar').addEventListener('click', () => {
    renderForm(view.formState)
  })
}

function calcular({ especie, peso, med, conc, sens, raw }) {
  const regra = getRegra(med, especie)
  const panel = document.getElementById('panel')
  const alertMsg = (msg) => {
    panel.insertAdjacentHTML('beforeend', `<div class=\"mt-4 rounded-md border border-red-200 bg-red-50 px-4 py-3 text-sm text-red-700\">${msg}</div>`)
  }

  if (!Number.isFinite(peso)) return alertMsg('Informe um peso v√°lido (n√∫mero).')
  if (peso <= 0) return alertMsg('Peso deve ser maior que zero.')
  if (peso < limitesPesoKg.min) return alertMsg(`Peso muito baixo. M√≠nimo: ${limitesPesoKg.min} kg.`)
  if (peso > limitesPesoKg.max) return alertMsg(`Peso muito alto. M√°ximo: ${limitesPesoKg.max} kg.`)
  if (!regra) return alertMsg('N√£o h√° protocolo cadastrado para esta combina√ß√£o de medicamento e esp√©cie.')
  if (raw.concStr && (!Number.isFinite(conc) || conc <= 0)) return alertMsg('Concentra√ß√£o inv√°lida. Informe um valor em mg/mL maior que 0.')

  const mgKg = regra.min === regra.max ? regra.min : (regra.min + regra.max) / 2
  const doseMgNum = round2(peso * mgKg)
  const volumeMlNum = raw.concStr ? round2(doseMgNum / conc) : null

  const faixaStr = regra.min === regra.max ? `${regra.min} mg/kg` : `${regra.min}‚Äì${regra.max} mg/kg (usado ${round2(mgKg)} mg/kg)`
  const alerta = (med === 'ivermectina' && especie === 'cachorro' && sens)
    ? 'Aten√ß√£o: Ivermectina pode ser contraindicada em ra√ßas sens√≠veis (MDR1).'
    : ''

  renderResultado({
    especie,
    med,
    doseMg: formatNumber(doseMgNum),
    volumeMl: volumeMlNum !== null ? formatNumber(volumeMlNum) : '',
    freq: regra.freq,
    dur: regra.dur,
    faixaStr,
    obs: regra.obs,
    alerta,
    formState: { especie, pesoStr: raw.pesoStr, med, concStr: raw.concStr, sensivel: sens },
  })
}

// Inicializa view
renderForm()


